<template>
  <div>
    <div class="wrapper is--search">
        <select
          :disabled="codeIsRunning"
          v-if="!addingDesmosUrl"
          class="search is--template-border w-form search_input large-blur"
          v-model="desmosUrl"
          @change="onSetDesmosId()"
        >
          <option
            disabled
            class="dropdown-list w-dropdown-list w--open"
            value=""
          >
            Select or insert Desmos Urls
          </option>
          <option
            class="dropdown-list w-dropdown-list w--open"
            v-for="(option, index) in desmosUrls"
            :value="option"
            :key="index"
          >
            <ul>
              <li class="w-embed">
                <label><span>{{index+1}} - </span>{{ option }}</label>
              </li>
            </ul>
          </option>
        </select>
        <div
          v-else 
          :style="codeIsRunning ? 'z-index: -1;': ''"
          class="search is--template-border w-form"
        >
          <input
            class="search_input w-input large-blur"
            autofocus
            v-model="newDesmosUrl"
            placeholder="new desmos URL"
            dark
          />
          <a
            class="icon-btn large-blur"
            @click="addingDesmosUrl = !addingDesmosUrl"
          >
            <i class="fa fa-arrow-left"></i>
          </a>
        </div>
        <div
          :style="codeIsRunning ? 'z-index: -1;': ''"
          class="little-row3"
        >
          <a
            class="icon-btn is--template-border large-blur"
            :class="!newDesmosUrl && addingDesmosUrl ? 'is--deselect' : null"
            @click="onAddNewDesmosUrl"
          >
            <i class="fa fa-plus"></i>
          </a>
          <a
            class="icon-btn is--template-border large-blur"
            @click="setFirstState()"
          >
            <img
              src="../images/refresh-button.svg"
              loading="lazy"
              alt=""
              class="icon-img is--large"
            />
          </a>
          <a
            class="state_actionbtn is--clear large-blur"
            @click="onDeleteDesmosUrl()"
          >
            <img
              src="../images/trash.svg"
              loading="lazy"
              alt=""
              class="icon-img is--xlarge"
            />
          </a>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  name: "DesmosUrlComp",
  props: {
      codeIsRunning: {
        type: Boolean,
        defaultValue: false,
      },
      stateList: {
        type: Array
      }
  },
  data: () => ({
    desmosUrl: '',
    desmosUrls: [],
    desmosId: 1,
    addingDesmosUrl: false,
    newDesmosUrl: '',
    firstStates: [],
  }),
  mounted () {
    const canvasID = this.$route.query.canvasID;
    if (canvasID) {
      this.desmosUrls = canvasID.split(',');
      this.desmosUrl = canvasID.split(',')[0];
      this.$emit('on-change-desmos-urls', this.desmosUrls);
      if (this.desmosUrl) this.onSetDesmosId();
    }
  },
  methods: {
    onAddNewDesmosUrl () {
      if (this.newDesmosUrl && this.isValidHttpUrl(this.newDesmosUrl) && !this.desmosUrls.includes(this.newDesmosUrl)) {
        this.desmosUrls.push(this.newDesmosUrl);
        this.desmosUrl = this.newDesmosUrl;
        this.newDesmosUrl = '';
        this.$emit('on-change-desmos-urls', this.desmosUrls);
        this.onSetDesmosId();
      } else if (this.addingDesmosUrl) {
        alert("Please insert a valid and unique url");
      }
      this.addingDesmosUrl = !this.addingDesmosUrl;
    },
    onDeleteDesmosUrl () {
      let numberOfComps = 0;
      for (const state of this.stateList) {
        state.transformation.forEach((st) => {
          if (st.desmosId === this.desmosId) {
            numberOfComps++
          }
        });
      }
      const confirmed = window.confirm('you have ' + numberOfComps + ' transformations with desmos url '+ this.desmosId +'. \n are you sure to remove it?');
      if (confirmed) {
        this.$emit('on-delete-desmosUrl', this.desmosId);
        if (this.desmosUrls.length > 1) {
          for (const urlIdx in this.desmosUrls) {
            if (
              (+urlIdx === this.desmosId - 1 || +urlIdx > this.desmosId - 1) && 
              window[`calculator${+urlIdx + 2}`]
            ) {
              const state = window[`calculator${+urlIdx + 2}`].getState();
              window[`calculator${+urlIdx + 1}`].setState(state);
            }
          }
        } else {
          const development = { border:false, invertedColors: true, showGrid: false, projectorMode: true, keypad:true, expressionsCollapsed: false, settingsMenu:true,lockViewport: false, fontSize:16,expressions:true};
          window['calculator1'].setState();
          window['calculator1'].updateSettings(development);
          this.desmosUrl = '';
        }
        this.desmosUrls.splice(this.desmosId-1, 1);
        this.firstStates.splice(this.desmosId-1, 1);
        this.desmosUrls[0] ? this.desmosUrl = this.desmosUrls[0]: '';
        this.$emit('on-change-desmos-urls', this.desmosUrls);
        this.onSetDesmosId();
      }
    },
    onSetDesmosId () {
      const id = this.desmosUrls.findIndex(a => a == this.desmosUrl)
      this.desmosId = id < 0 ? 1: id+1;
      window.desmosId = this.desmosId;
      this.$emit('on-change-desmosId', this.desmosId);
      if (this.desmosUrl) {
        window.desmosUrl = this.desmosUrl;
        setTimeout(() => {
          if (
            window[`calculator${this.desmosId}`] &&
            !this.firstStates[this.desmosId-1]
          ) {
            this.setFirstState();
          }
        }, 10);
      }
    },
    async setFirstState () {
      window.desmosId = this.desmosId;
      this.$emit('on-change-desmosId', this.desmosId)
      window.desmosUrl = this.desmosUrl;
      if (this.desmosUrl !== '1') {
        const loader = this.$loading.show();
        try {
          const url = new URL(this.desmosUrl);
          const response = await this.$axios({ url: url.pathname, baseURL: url.origin }, {
            headers: {
              accept: 'application/json',
            }
          });
          const firstState = response.data.state;
          this.firstStates[this.desmosId-1] = JSON.stringify(firstState);
          window[`calculator${this.desmosId}`]?.setState(firstState);
          loader.hide()
        } catch (error) {
          console.log(error);
          alert(error);
          loader.hide();
        }
        loader.hide();
      }
    },
    isValidHttpUrl(string) {
      if (!string) return true
      let url;
      try {
        url = new URL(string);
      } catch (_) {
        return false;  
      }
      if (url) {
        return true;
      }
    }
  }
}
</script>